.about{

    overflow: hidden;
    overflow-y: scroll;
    position: relative;
    height: 80%;
    width: 80%;
    margin: 20px;
    padding: 30px;
    background-color: rgba(60, 61, 58, .7);
    font-family: 'Ubuntu', sans-serif;
    font-weight: normal;
    color: white;
    font-size: 20px;
    border-radius: 10px;

}

@media screen and (min-width:426px)
{
    .about{
    padding: 30px 50px;
    font-size: 25px;

    }
}